@import "@material/rtl/mixins";

@mixin mdc-slideable-drawer {
  height: 100%;
  transform: translateX(-107%);
  transform: translateX(calc(-100% - 20px));
  will-change: transform;
}

@mixin mdc-slideable-drawer-rtl {
  transform: translateX(107%);
  transform: translateX(calc(100% + 20px));
}

@mixin mdc-slideable-drawer-open {
  transform: none;
}



